import React from 'react'

// material-ui
import Grid from '@material-ui/core/Grid'

// custom component
import ListCard from './ListCard'

/**
 * Responsive GridList container component
 * @param edges
 * @param location
 * @returns {*}
 * @constructor
 */
export default function GridList({ edges, location }) {
  return (
    <Grid container spacing={2} justify="center">
      {edges.map(edge => {
        const {
          node: {
            excerpt,
            timeToRead,
            fields: { slug },
            frontmatter: {
              title,
              subtitle,
              date,
              publishedBy,
            }
          }
        } = edge

        return (
          <Grid item key={slug} xs={12} sm={12} md={6} lg={4}>
            <ListCard
              title={title}
              subtitle={subtitle}
              excerpt={excerpt}
              publishedDate={date}
              publishedBy={publishedBy}
              timeToRead={timeToRead}
              path={slug}
              referrer={location.pathname}
            />
          </Grid>
        )
      })}
    </Grid>
  )
}
