import React from 'react'

// material-ui
import Box from '@material-ui/core/Box'
import Button from '@material-ui/core/Button'
import Card from '@material-ui/core/Card'
import CardHeader from '@material-ui/core/CardHeader'
import CardActions from '@material-ui/core/CardActions'
import CardContent from '@material-ui/core/CardContent'
import Typography from '@material-ui/core/Typography'
import { withStyles } from '@material-ui/core/styles'
import CardActionArea from '@material-ui/core/CardActionArea'

// custom component
import Link from '../Links/Link'

const styles = {
  card: {
    height: '100%'
  },
  actions: {
    justifyContent: 'space-between'
  },
  cardMedia: {
    height: '150px'
  },
  titleText: {
    textAlign: 'center'
  }
}

/**
 * ListCard component
 * @param classes
 * @param title
 * @param subtitle
 * @param excerpt
 * @param publishedDate
 * @param timeToRead
 * @param path
 * @param referrer
 * @returns {*}
 * @constructor
 */
function ListCard({
  classes,
  title,
  subtitle,
  excerpt,
  publishedDate,
  timeToRead,
  path,
  referrer
}) {
  return (
    <CardActionArea component={Link} state={{ referrer: referrer }} to={path}>
      <Card className={classes.card}>
        <CardHeader
          disableTypography={true}
          title={<Typography variant="h6" color="primary">{title}</Typography>}
          subheader={<Typography variant="body2">{subtitle}</Typography>}
        />
        <CardContent>
          <Typography variant="body2">{excerpt}</Typography>
        </CardContent>
        <CardActions disableSpacing className={classes.actions}>
          <Box component="div">
            <Typography component="div" variant="caption">
              {'Date: ' + publishedDate}
            </Typography>
            <Typography component="div" variant="caption">
              {'Time to read: ' + timeToRead + ' min'}
            </Typography>
          </Box>
          <Box component="div">
            <Button size="small" color="secondary">
              Read more ...
            </Button>
          </Box>
        </CardActions>
      </Card>
    </CardActionArea>
  )
}

export default withStyles(styles)(ListCard)
